import React, { useEffect, useState } from 'react'
import 'react-slideshow-image/dist/styles.css'
import { Fade, Slide } from 'react-slideshow-image';

const Background = () => {
    const API_KEY = "ebdbb3f2b0075bfa9b6efb62a48a3aa3"
    const popularMoviesURL = `https://api.themoviedb.org/3/movie/popular?api_key=${API_KEY}&language=it`
    let [popularMoviesImages, setPopularMoviesImages] = useState([]);
    let [currentIndex, setCurrentIndex] = useState(0);
    let [isFading, setIsFading] = useState(false);

    useEffect(() => {
        const searchImages = () => {
            fetch(popularMoviesURL)
                .then(response => response.json())
                .then(data => {
                    let popularMovies = data.results
                    let images = []
                    popularMovies.forEach(movie => {
                        images.push(`https://image.tmdb.org/t/p/w500${movie.poster_path}`)
                    });
                    setPopularMoviesImages(images);
                })
        }
        setTimeout(searchImages, 50);

    }, [])

    useEffect(() => {
        document.body.style.backgroundImage = `url(${popularMoviesImages[currentIndex]})`;
        document.body.style.backgroundSize = "cover";
        document.body.style.backgroundPosition = "center";

    }, [currentIndex]);

    return (
        <div className="slide-container">

            <Fade duration={5000} transitionDuration={2000} autoplay={true} arrows={false} infinite={true}>
                {popularMoviesImages.map((fadeImage, currentIndex) => (
                    <div style={{backgroundSize: "cover", width: '100%', height: "100%"}}  key={currentIndex}>
                        <img src={fadeImage} />
                    </div>
                ))}
            </Fade>
        </div>
    )
}

export default Background