const initialState = {
  isAuthenticated: document.cookie.split(";").some((item) => item.trim().startsWith('refreshToken=')),
  user: {}
};

function authReducer(state = initialState, action) {
  switch (action.type) {
    case 'LOGIN':
      
      return {
        ...state,
        isAuthenticated: true,
        user: action.payload,
      };
    case 'LOGOUT':
      document.cookie = 'refreshToken=; path=/; expires=Thu, 01 Jan 1970 00:00:01 GMT';
      return {
        ...state,
        isAuthenticated: false,
        user: {}
      };
    default:
      return state;
  }
}

export default authReducer;