/* eslint-disable react-hooks/exhaustive-deps */
import React, { useState, useEffect } from 'react'
import axios from 'axios';
import jwt_decode from "jwt-decode";
import { useNavigate } from 'react-router-dom';
import { useSelector } from 'react-redux';
import { OverlayScrollbarsComponent } from 'overlayscrollbars-react';
import Modal from 'react-modal';
import useLists from '../hook/useLists';
import Typed from "react-typed"
import ChangePassword from './ChangePassword';
import VideoPlayer from './VideoPlayer';
import ReactPlayer from 'react-player';

const Dashboard = () => {
    const [token, setToken] = useState('');
    const [expire, setExpire] = useState('');
    const [email, setEmail] = useState("")
    const [users, setUsers] = useState([]);
    const [showModalChangePass, setShowModalChangePass] = useState(false);
    const navigate = useNavigate();

    const {
        isAlertOpen, isListOpen, isModalOpen, loading, createdList, listname,
        state, results, selectedResult, query, allGenres, sortedResults, sortType,
        name, lists, isDeleted, isEdited, isAuthenticated, isLoaded,
        setSortType, sortResults, setSortedResults, handleOpenModal,
        handleCloseModal, handleCreateList, handleRemoveResult,
        handleListNameChange, handleSaveList, handleSubmit, fetchAllGenres,
        formatItalianDate, setQuery, setListname, setLoading,
        handleGetLists, handleEditListName, handleDeleteList, handleDeleteItem,
        setName, setLists, setIsDeleted, setIsEdited, setIsAlertOpen,
        setIsLoaded, sortLists } = useLists();
    const [isPswChanged, setIsPswChanged] = useState(false)
    const [showModalDetails, setShowModalDetails] = useState(false)
    const [selectedIndex, setSelectedIndex] = useState({});

    const openModalDetails = (item) => {
        setSelectedIndex(item);
        setShowModalDetails(true);
    };

    const closeModalDetails = () => {
        setShowModalDetails(false);
    };

    const openModalChangePass = () => {
        setShowModalChangePass(true);
    }
    const closeModalChangePass = () => {
        setShowModalChangePass(false)
    };



    const bg = {
        overlay: {
            background: "transparent"
        }
    };

    useEffect(() => {
        console.log("token")
        refreshToken();
        getUsers();
    }, []);


    const refreshToken = async () => {
        try {
            const response = await axios.get('https://gamediahub.it:5000/token');
            const accessToken = response.data.accessToken;
            localStorage.setItem('accessToken', accessToken);
            setToken(accessToken);
            const decoded = jwt_decode(accessToken);
            setName(decoded.name);
            setEmail(decoded.email)
            setExpire(decoded.exp);
            localStorage.setItem('name', name);
            console.log("cookie")
        } catch (error) {
            if (error.response) {
                console.log("errore")

            }
        }
    }

    const axiosJWT = axios.create();

    axiosJWT.interceptors.request.use(async (config) => {
        const currentDate = new Date();
        if (expire * 1000 < currentDate.getTime()) {
            const response = await axios.get('https://gamediahub.it:5000/token');
            config.headers.Authorization = `Bearer ${response.data.accessToken}`;
            setToken(response.data.accessToken);
            const decoded = jwt_decode(response.data.accessToken);
            setName(decoded.name);
            setEmail(decoded.email)
            setExpire(decoded.exp);
            localStorage.setItem('accessToken', response.data.accessToken);
        }
        return config;
    }, (error) => {
        return Promise.reject(error);
    });

    const getUsers = async () => {
        const response = await axiosJWT.get('https://gamediahub.it:5000/users', {
            headers: {
                Authorization: `Bearer ${token}`
            }
        });
        setUsers(response.data);
    }

    useEffect(() => {
        if (lists.length) {
            sortLists();
            setLoading(true);
        }
        setTimeout(() => {
            setLoading(false);
        }, 3500);
    }, [sortType, results]);

    return (
        <div className="ProfileContainer">
            <div className='Bg'>
                <div className='TableContainer'>
                    {/* MESSAGGIO DI BENVENUTO */}
                    <div className='userName'>
                        <Typed
                            startDelay={2000}
                            typeSpeed={20}
                            fadeOut={false}
                            backSpeed={20}
                            smartBackspace={true}
                            showCursor={false}
                            cursorChar="|"
                            strings={[
                                `<h1>Bentornato: ${name}</h1>`,
                            ]}
                        />
                        <div className="RegisterText changePsw">Clicca <span onClick={openModalChangePass}>QUI</span> per resettare la Password.</div>
                    </div>
                    {/* TABELLA */}
                    <div className="Table">

                        {/* COLONNE NOME EMAIL */}
                        <div className='TableTitles'>
                            <div>Nome utente</div>
                            <div>Indirizzo Email</div>
                        </div>
                        <div className='TableBottom'>
                            {/* RIGHE NOME EMAIL */}
                            <div className='TableInfos'>
                                <div className='RowInfo'>{name}</div>
                                <div className='RowInfo'>{email}</div>

                            </div>
                            {/* PULSANTI "CARICA" E "ORDINA" */}
                            <div className='BtnContainer'>
                                <div className='ButtonLists'>
                                    {/* PULSANTE "CARICA" */}
                                    <div className='LoadBtn EffectBtn' onClick={handleGetLists}>
                                        <svg>
                                            <rect x="0" y="0" fill="none" width="100%" height="100%" />
                                        </svg>
                                        <div>CARICA LE TUE LISTE</div>
                                    </div>
                                    {/* PULSANTE "ORDINA PER" */}
                                    <div className="FilterContainer">
                                        <select className="FilterBy EffectBtn" onChange={(event) => setSortType(event.target.value)}>

                                            <option selected value="0" >
                                                <span>Ordina Per...</span>
                                            </option>
                                            <option value="title">Titolo</option>
                                            <option value="updatedAt">Ultima Modifica</option>
                                            <option value="length">Lunghezza</option>
                                        </select>
                                    </div>
                                </div>

                            </div>
                        </div>
                    </div>
                </div>

                {/* CONTENITORE LISTE */}
                <OverlayScrollbarsComponent className='scrollbar'
                    element="span"
                    options={{ scrollbars: { autoHide: 'scroll', theme: "os-theme-light", } }}
                    defer>

                    <div className="ListsContainer">
                        {/* CARICAMENTO */}
                        {loading ?
                            <div className='loading'>
                                <div class="circle loader"></div>
                                <p className='textLoading'>CARICAMENTO...</p>
                            </div> :
                            //MAPPA LISTE
                            lists.length ? lists.map((result, index) => (
                                //CONTENITORE LISTA
                                <div className="ListsMap" key={index}>
                                    {/* NOME LISTA */}
                                    <div className="ListsName">
                                        <h2>{result.listname}</h2>
                                        {/* CONTENITORE PULSANTI */}
                                        <div className='BtnContainer'>
                                            <div className='ButtonLists'>
                                                {/* PULSANTE MODIFICA NOME LISTA */}
                                                <div className='EditBtn EffectBtn' onClick={() => handleEditListName(
                                                    result.id, prompt("Inserisci il nuovo nome della lista"))}>
                                                    <svg>
                                                        <rect x="0" y="0" fill="none" width="100%" height="100%" />
                                                    </svg>
                                                    <div>MODIFICA NOME</div>
                                                </div>
                                                {/* PULSANTE ELIMINA LISTA */}
                                                <div className='DelBtn EffectBtn' onClick={() => handleDeleteList(result.id)}>
                                                    <svg>
                                                        <rect x="0" y="0" fill="none" width="100%" height="100%" />
                                                    </svg>
                                                    <div>ELIMINA LISTA</div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                    {/* MAPPA RISULTATI DELLA LISTA */}
                                    {result.item.map((item) => (
                                        // CONTENITORE ITEM
                                        <div className="ItemMap" key={item.id} style={{
                                            backgroundImage: `url(${item.media_type === "movie" || item.media_type === "tv" ?
                                                `https://image.tmdb.org/t/p/w500${item.sfondo || item.poster || ""}`
                                                : item.copertina ? `${item.copertina}` : ""})`,
                                        }}>
                                            <div className="ItemContent">
                                                {/* CONTENUTO OGGETTO */}
                                                <div className='ItemTitle'>
                                                    {item.titolo_film && <p><span className='ItemName'>{item.titolo_film}</span> <span onClick={() => openModalDetails(item)} className='ItemDetails'>Altro..</span></p>}
                                                    {item.titolo_tv && <p><span className='ItemName'>{item.titolo_tv}</span> <span onClick={() => openModalDetails(item)} className='ItemDetails'>Altro..</span></p>}
                                                    {item.titolo_gioco && <p><span className='ItemName'>{item.titolo_gioco}</span> <span onClick={() => openModalDetails(item)} className='ItemDetails'>Altro..</span></p>}
                                                    <Modal className="PopUpContainer ModalReg" closeTimeoutMS={500}
                                                        style={bg} isOpen={showModalDetails} onRequestClose={closeModalDetails}>
                                                        <div className='PopUp Details' style={{
                                                            backgroundImage: `url(${selectedIndex.media_type === "movie" || selectedIndex.media_type === "tv" ?
                                                                `https://image.tmdb.org/t/p/w500${selectedIndex.sfondo || selectedIndex.poster || ""}`
                                                                : selectedIndex.copertina ? `${selectedIndex.copertina}` : ""})`,
                                                        }}><div className='BgWhite'>
                                                                <div className='PopUpTitle'>Dettagli</div>
                                                                <div className='Bg'>
                                                                    <div className="Title">{selectedIndex.titolo_film ? selectedIndex.titolo_film
                                                                        : selectedIndex.titolo_tv ? selectedIndex.titolo_tv
                                                                            : selectedIndex.titolo_gioco}
                                                                    </div>
                                                                    <div className="Content">
                                                                        <div className="Genre"><p>Genere: <span>{selectedIndex.genere_media ? selectedIndex.genere_media.map(genere => allGenres[genere]).join(', ') : selectedIndex.genere_gioco ? selectedIndex.genere_gioco : ""}</span></p></div>
                                                                        {selectedIndex.media_type ? "" :
                                                                            <div className="Platform"><p>Piattaforma: <span>{selectedIndex.platform ? selectedIndex.platform.split(', ').map(item => {
                                                                                switch (item) {
                                                                                    case 'PlayStation 3':
                                                                                        return 'PS3';
                                                                                    case 'PlayStation 4':
                                                                                        return 'PS4';
                                                                                    case 'PlayStation 5':
                                                                                        return 'PS5';
                                                                                    case 'Xbox 360':
                                                                                        return 'x360';
                                                                                    case 'Xbox One':
                                                                                        return 'XOne';
                                                                                    case 'PC (Microsoft Windows)':
                                                                                        return "PC"
                                                                                    default:
                                                                                        return selectedIndex.platform;
                                                                                }
                                                                            }).join(', ') : ""}</span></p></div>}
                                                                        <div className="Date"><p>Data: <span>{selectedIndex.data_film ? formatItalianDate(selectedIndex.data_film) : selectedIndex.data_tv ? formatItalianDate(selectedIndex.data_tv) : formatItalianDate(selectedIndex.data_gioco)}</span></p></div>
                                                                        <div className="Vote">{selectedIndex.voto ? (<p>Voto: <span>{Math.floor(selectedIndex.voto * 100) / 100}</span></p>) : (<p>Metacritic: <span>{selectedIndex.metacritic}</span></p>)}</div>
                                                                        <div className="Video">{selectedIndex.video_media ? (
                                                                            <VideoPlayer videoUrl={`https://www.youtube.com/watch?v=${selectedIndex.video_media}`} />
                                                                        ) : selectedIndex.video_gioco ? (
                                                                            <VideoPlayer videoUrl={`https://www.youtube.com/watch?v=${selectedIndex.video_gioco}`} />
                                                                        ) : (
                                                                            <p>Nessun video disponibile</p>
                                                                        )
                                                                        }</div>
                                                                    </div>
                                                                </div>
                                                            </div>
                                                            <div className='PopUpCloser' onClick={() => closeModalDetails(item)}>
                                                                X
                                                            </div>
                                                        </div>
                                                    </Modal>
                                                    <div className='buttonListRemove'>
                                                        <div className='RemoveBtn'>
                                                            <div onClick={() => handleDeleteItem(result.id, item.id)}><i class="fa-solid fa-xmark"></i></div>
                                                        </div>
                                                    </div>
                                                </div>
                                                <div className='ItemBottom'>
                                                    <div className='ItemType'>
                                                        {item.media_type ? (item.media_type === "movie" ? "Film" : "Serie TV")
                                                            : (item.gioco ? "Videogioco" : "")}
                                                    </div>
                                                    <div className='ItemVote'>
                                                        {item.voto ? (`VOTO: ${Math.floor(item.voto * 10) / 10}`) : (`METACRITIC: ${Math.floor(item.metacritic * 1) / 1}`)}
                                                    </div>

                                                </div>
                                            </div>


                                        </div>

                                    ))}
                                </div>
                            )) : <div className='ListsMap'>NESSUNA LISTA CARICATA</div>
                        }
                    </div>

                </OverlayScrollbarsComponent>
                {/* POPUP "NOME MODIFICATO" E "LISTA ELIMINATA" */}
                <Modal
                    style={{
                        zIndex: "9999 !important", position: "fixed", overlay: {
                            backgroundColor: 'transparent'
                        }
                    }}
                    className="PopUpContainer ModalLogin animated bounceIn Alert"
                    isOpen={isAlertOpen} onRequestClose={handleCloseModal}
                    contentLabel="Example Modal">
                    <div className='PopUpOv'>
                        {isEdited && <p>RICARICA LE LISTE PER AGGIORNARE IL NOME</p>}
                        <button className='PopUpCloser' onClick={handleCloseModal}>X</button>
                    </div>
                </Modal>
                <Modal className="PopUpContainer ModalReg" closeTimeoutMS={500}
                    style={bg} isOpen={showModalChangePass} onRequestClose={showModalChangePass}>
                    <div className='PopUp'>
                        <div className='PopUpTitle'>Cambia Password</div>
                        <ChangePassword closeModal={closeModalChangePass} />
                        <div className='PopUpCloser' onClick={closeModalChangePass}>
                            X
                        </div>
                    </div>
                </Modal>

            </div>
        </div >)

}

export default Dashboard