import axios from "axios";
import { useState } from "react";

const ForgotPassword = ({ closeModal }) => {
    const [email, setEmail] = useState("");
    const [msg, setMsg] = useState('');
    const [formSubmitted, setFormSubmitted] = useState(false)

    const handleSubmit = async (e) => {
        e.preventDefault();
        try {
            const { data }  = await axios.post("https://gamediahub.it:5000/forgotPassword", {
                email
            });
            setFormSubmitted(true);
            if(data){
                setMsg(data.msg)
                closeModal();
            }
            closeModal();
            
        } catch (error) {
            if (error.response) {
                setMsg(error.response.data.msg);
                console.log(error.response.data.msg)
              }
        }
    };

    return (
        <form className="formReset" onSubmit={handleSubmit}>
            <div className="formRow">
                <div className="formInput">
                    <label>
                        <input required type="email" value={email} onChange={e => setEmail(e.target.value)} placeholder="email" />
                    </label>
                </div>
            </div>
            <div className='PopUpText'>Compila i campi correttamente</div>
            <button className='LoginButton' type="submit">Invia Email</button>
            {msg && <p className="msg">{msg}</p>}
        </form>
    );
};

export default ForgotPassword