import React, { useEffect, useState } from "react";
import axios from "axios";
import useLists from '../hook/useLists';
import Modal from 'react-modal';
import useRefreshToken from "../hook/useToken";
const ChangePassword = ({ closeModal }) => {
    const [name, setName] = useState("");
    const [currentPassword, setCurrentPassword] = useState("");
    const [newPassword, setNewPassword] = useState("");
    const [confPassword, setConfPassword] = useState("");
    const [msg, setMsg] = useState('');
    const [formSubmitted, setFormSubmitted] = useState(false)
    const {token, expire, email, refreshToken} = useRefreshToken()

    useEffect(() => {
        refreshToken();
    }, []);
    const handleSubmit = async (e) => {
        
        
        e.preventDefault();
        try {
            const headers = {
                'Authorization': `${token}`,
                "Content-Type": "application/json",
            };
            
            const { data } = await axios.post("https://gamediahub.it:5000/changePassword", {
                name: name,
                currentPassword: currentPassword,
                newPassword: newPassword,
                confPassword: confPassword,
            
            }, { headers });
            setFormSubmitted(true);
            if (data) {
                setMsg(data.msg)
            }
            setTimeout(() => {
                closeModal();
            }, 3000);
        } catch (error) {
            setMsg(error.response.data.msg)
        }


    };

    return (
        <div className='Bg'>
            <form onSubmit={handleSubmit}>
                <div className="formRow">
                    <div className="formInput">
                        <label>
                            <input required type="text" value={name} onChange={e => setName(e.target.value)} placeholder="username" />
                        </label>
                    </div>
                </div>
                {/* <input type="text" value={username} onChange={e => setUsername(e.target.value)} placeholder="Username" /> */}
                <div className="formRow">
                    <div className="formInput">
                        <label>

                            <input required type="password" value={currentPassword} onChange={e => setCurrentPassword(e.target.value)} placeholder="Password attuale" />
                        </label>
                    </div>
                </div>
                <div className="formRow">
                    <div className="formInput">
                        <label>

                            <input required type="password" value={newPassword} onChange={e => setNewPassword(e.target.value)} placeholder="Nuova Password" />
                        </label>
                    </div>
                </div>
                <div className="formRow">
                    <div className="formInput">
                        <label>

                            <input required type="password" value={confPassword} onChange={e => setConfPassword(e.target.value)} placeholder="Conferma Password" />
                        </label>
                    </div>
                </div>
                <div className='PopUpText'>Compila i campi correttamente</div>
                <button className='LoginButton' type="submit">Cambia Password</button>
                {msg && <p>{msg}</p>}
            </form>

        </div>
    );
};

export default ChangePassword;