import React, { useEffect, useState } from 'react'
import axios from 'axios';
import { Link, Route, Routes, useLocation, useNavigate } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import Login from './Login';
import Modal from 'react-modal';
import Registration from './Registration';
import jwt_decode from "jwt-decode";
import Burger from './Burger';
import BackButton from './BackButton';
import ChangePassword from './ChangePassword';
import logo from "../assets/img/logo.png";

const Navbar = () => {
    const [showModalLogin, setShowModalLogin] = useState(false);
    const [showModalSignup, setShowModalSignup] = useState(false);
    const isAuthenticated = useSelector(state => state.isAuthenticated);
    const dispatch = useDispatch();
    const navigate = useNavigate();
    const [name, setName] = useState('');
    const [token, setToken] = useState('');
    const [expire, setExpire] = useState('');
    const [users, setUsers] = useState([]);
    const openModalLogin = () => setShowModalLogin(true);
    const closeModalLogin = () => {
        setShowModalLogin(false)
    };
    const openModalSignup = () => {
        setShowModalSignup(true);
        setShowModalLogin(false)
    }
    const closeModalSignup = () => setShowModalSignup(false)


    const bg = {
        overlay: {
            background: "transparent"
        }
    };



    useEffect(() => {
        refreshToken();
    }, []);


    const refreshToken = async () => {
        try {
            const response = await axios.get('https://gamediahub.it:5000/token', {
                withCredentials: true
            });
            const accessToken = response.data.accessToken;
            setToken(accessToken);
            const decoded = jwt_decode(accessToken);
            setName(decoded.name);
            setExpire(decoded.exp);
            console.log("cookie")
        } catch (error) {
            if (error.response) {
                console.log("errore")

            }
        }
    }

    const axiosJWT = axios.create();

    axiosJWT.interceptors.request.use(async (config) => {
        const currentDate = new Date();
        if (expire * 1000 < currentDate.getTime()) {
            const response = await axios.get('https://gamediahub.it:5000/token', {
                withCredentials: true
            });
            config.headers.Authorization = `Bearer ${response.data.accessToken}`;
            setToken(response.data.accessToken);
            const decoded = jwt_decode(response.data.accessToken);
            setName(decoded.name);
            setExpire(decoded.exp);
        }
        return config;
    }, (error) => {
        return Promise.reject(error);
    });

   

    const Logout = async () => {
        try {
            dispatch({ type: 'LOGOUT' });
            await axios.delete('https://gamediahub.it:5000/logout');
            navigate("/");
            console.log("logout eseguito");
        } catch (error) {
            console.log(error);
        }
    }

    return (

        <nav className="navbar">

            <div className="navContainer">


                <div className="navbarMenu">


                    <div className="navbarStart">
                        <div style={{width:"25%"}}><Burger /></div>
                        <div className='Buttons'>
                            <BackButton />
                            <div className='buttonContainer'>
                                <a href='/' className="navButtonH iconFill">
                                    <i class="fa fa-home"></i>
                                    <span className="signUpText">HOME</span>

                                </a>
                            </div>
                            {!isAuthenticated &&
                                <div className='buttonContainer'>
                                    <div className='navButtonLogin iconLogin' onClick={openModalLogin}>

                                        <i class="fa-solid fa-right-to-bracket"></i>
                                        <span className='signUpText'>LOGIN</span>

                                    </div>
                                </div>
                            }

                            {isAuthenticated &&
                                <div className='buttonContainer'>
                                    <div className='navButtonLogout iconLogout' onClick={Logout}>
                                        <i class="fa-solid fa-right-to-bracket"></i>
                                        <span className='signUpText'>
                                            ESCI
                                        </span>
                                    </div>
                                </div>
                            }
                            {!isAuthenticated &&
                                <Modal className="PopUpContainer ModalLogin animated bounceIn" closeTimeoutMS={500}
                                    style={bg} isOpen={showModalLogin} onRequestClose={showModalLogin}>
                                    <div className='PopUp'>
                                        <div className='PopUpTitle'>LOGIN</div>
                                        <Login onClick={closeModalLogin} />

                                        <div className='PopUpCloser' onClick={closeModalLogin}>
                                            X
                                        </div>
                                        <div className="RegisterText">Se non hai un account <span onClick={openModalSignup}>REGISTRATI QUI</span>
                                        </div>

                                    </div>
                                </Modal>}


                            <Modal className="PopUpContainer ModalReg" closeTimeoutMS={500}
                                style={bg} isOpen={showModalSignup} onRequestClose={showModalSignup}>
                                <div className='PopUp'>
                                    <div className='PopUpTitle'>Registrazione</div>
                                    <Registration closeModal={closeModalSignup} />
                                    <div className='PopUpCloser' onClick={closeModalSignup}>
                                        X
                                    </div>
                                </div>
                            </Modal>

                            {isAuthenticated &&
                                <Link to="/profile">
                                    <div className='buttonContainer'>
                                        <div className='profile iconFillProfile'>

                                            <i class="fa-regular fa-user"></i>
                                            <span className='signUpText'>
                                                PROFILO
                                            </span>
                                        </div>
                                    </div>
                                </Link>
                            }
                        </div>
                        <div className='logoNav'><img src={logo} alt="logo" /></div>
                    </div>
                </div>
            </div>
        </nav >

    )
}

export default Navbar