import axios from "axios"
import { useEffect, useState } from "react"
import { useSelector } from "react-redux";
import { useNavigate, useParams } from "react-router-dom"
import Typed from "react-typed"
import ResendVerification from './ResendVerification';
const VerifyAccount = () => {
    const [verificated, setVerificated] = useState(false);
    const navigate = useNavigate()
    const { token } = useParams()
    const [error, setError] = useState("");

    useEffect(() => {
        const handleVerification = async () => {
            try {
                const response = await axios.get(`https://gamediahub.it:5000/verify/${token}`);
                const data = response.data;
                setVerificated(true);
            } catch (e) {
                if(e) {
                    setError(e.response.data.msg);
                }
                
            }
        };
        
        handleVerification();
    }, [token]);

    useEffect(() => {
        if (verificated) {
            setTimeout(() => {
                navigate("/");
            }, 6000);
        }
    }, [verificated, navigate]);

    return (
        <div className="LandingContainer">
            {verificated ? (
                <div className="verifyAccount">
                   <Typed
                            
                            typeSpeed={50}
                            fadeOut={false}
                            showCursor={true}
                            cursorChar="_"
                            strings={[
                                `ACCOUNT VERIFICATO... VERRAI REINDIRIZZATO ALLA HOME TRA 2 SECONDI`]}
                        />
                </div>
            ) : (
                <div className="verifyAccount">
                    {error && <p>Errore: {error}</p>}
                    <h3>Account non verificato, puoi richiedere un nuovo link di attivazione</h3>
                    
                    {error && <ResendVerification />}
                </div>
            )}
        </div>
    )
};



export default VerifyAccount