import axios from "axios";
import Cookies from "js-cookie";
import { useEffect, useState } from "react";
import { useSelector } from "react-redux";

const useLists = () => {
    const [theme, setTheme] = useState({});
    const [videos, setVideos] = useState({});
    const [query, setQuery] = useState('');
    const [results, setResults] = useState([]);
    const [loading, setLoading] = useState(false);
    const [isModalOpen, setIsModalOpen] = useState(false);
    const [isListOpen, setIsListOpen] = useState(false);
    const [createdList, setCreatedList] = useState([]);
    const [isAlertOpen, setIsAlertOpen] = useState(false);
    const [listname, setListname] = useState("");
    const [allGenres, setAllGenres] = useState({});
    const [selectedResult, setSelectedResult] = useState({});
    const [state, setState] = useState({
        listname: '',
        list: []
    });
    const [sortedResults, setSortedResults] = useState(results)
    const [sortType, setSortType] = useState('popularity');
    const [name, setName] = useState('');
    const [lists, setLists] = useState([]);
    const [isDeleted, setIsDeleted] = useState(false)
    const [isEdited, setIsEdited] = useState(false)
    const isAuthenticated = useSelector(state => state.isAuthenticated);
    const [isLoaded, setIsLoaded] = useState(false)
    const [isListSaved, setIsListSaved] = useState(false)
    const [isSelected, setIsSelected] = useState(false)
    const [isBtnActive, setIsBtnActive] = useState(false)
    const [guid, setGuid] = useState({})
    const [trailer, setTrailer] = useState({})

    // RECUPERA E MODIFICA LISTE ACCOUNT

    const handleGetLists = async (e) => {
        if (isAuthenticated) {
            const token = localStorage.getItem("jwtToken");
            const headers = {
                'Authorization': `Bearer ${token}`,
                'Content-Type': 'application/json'
            };
            setLoading(true)
            setTimeout(async () => {
                const response = await axios.get(`https://gamediahub.it:5000/lists/${name}`, { headers });
                const data = response.data;
                console.log(data)
                if (data.success) {
                    setLists(data.lists);
                    setLoading(false)
                    const items = JSON.stringify(data.lists)
                    console.log(data.lists)
                } else {
                    console.log("Errore durante il recupero delle liste");

                }
            }, 2500);
        } else {
            console.log("L'utente deve accedere");
        }
    };

    const handleEditListName = (id, newListName) => {
        // Chiamata API per modificare il nome della lista
        axios.patch(`https://gamediahub.it:5000/lists/save/${id}`, { listname: newListName })
            .then(res => {
                // Aggiornare lo stato della lista con il nuovo nome

                setLists(prevLists => prevLists.map(list => {
                    if (list.id === id) {


                        return { ...list, listname: newListName };
                    }
                    return list;
                }));

            })
            .catch(err => {
                console.log(err)
            });
        console.log(id)
        setIsEdited(true)
        setIsAlertOpen(true)
        setTimeout(() => {
            setIsEdited(false)
            setIsAlertOpen(false)
        }, 3500);
    };


    const handleDeleteList = (id) => {
        console.log(id)
        axios.delete(`https://gamediahub.it:5000/lists/${id}`)
            .then(res => {
                // Aggiornare lo stato della lista rimuovendo l'elemento con l'id corrispondente
                setLists(prevLists => prevLists.filter(lists => lists.id !== id));
            })
            .catch(err => {
                console.log(err)
            });
        setIsAlertOpen(true)
        setIsDeleted(true)
        setTimeout(() => {
            setIsDeleted(false)
            setIsAlertOpen(false)
        }, 3500);
    };

    const handleDeleteItem = (id, itemId) => {
        console.log(id)
        console.log(itemId)
        console.log(lists)
        if (isAuthenticated) {
            axios.delete(`https://gamediahub.it:5000/lists/${id}/items/${itemId}`)
                .then(res => {
                    // Rimuovi l'item dallo stato locale
                    setLists(lists => lists.map(list => {
                        if (list.id === id) {
                            return { ...list, item: list.item.filter(item => item.id !== itemId) };
                        }
                        return list;
                    }));
                })
                .catch(err => {
                    console.log(err)
                });
        }
    };


    // APRI E CHIUDI POPUP

    const handleCloseModal = () => {
        if (isModalOpen) {
            setIsModalOpen(false);
            console.log(isModalOpen)
        } else if (isListOpen) {
            setIsListOpen(false)
        } else {
            setIsAlertOpen(false)
        }
    }

    const handleOpenModal = (result) => {
        setSelectedResult(result);
        setIsModalOpen(true);
        console.log("modalisopen")
    }

    // CREA, MODIFICA, SALVA, RIAPRI LISTE

    const handleOpenList = () => {
        setIsListOpen(!isListOpen);
    }

    const handleCreateList = (result) => {
        if (createdList.includes(result)) {
            setIsAlertOpen(true);
            setIsSelected(true)

            setTimeout(() => {
                setIsSelected(false)
                setIsAlertOpen(false)
            }, 3000);

        } else {
            console.log(createdList)
            setCreatedList([...createdList, result]);
            setIsListOpen(true);

        }
    }

    const handleRemoveResult = (result) => {
        setCreatedList(createdList.filter(r => r.id !== result.id));
    }

    const handleListNameChange = (e) => {
        setListname(e.target.value);
    };

    const handleSaveList = async (e) => {
        e.preventDefault();
        const token = localStorage.getItem("jwtToken");
        const name = Cookies.get('name');
        const username = name
        const headers = {
            'Authorization': `Bearer ${token}`,
            "Content-Type": "application/json",
        };
        // const { listname, list } = state;
        console.log(name)
        console.log(username)
        const itemsToSave = createdList.map(item => ({
            id: item.id,
            //titolo
            titolo_film: item.original_title,
            titolo_tv: item.name,
            titolo_gioco: item.title_game,
            //voto
            voto: item.vote_average,
            metacritic: item.vote,
            //media
            video_media: item.video,
            video_gioco: item.video,
            sfondo: item.backdrop_path,
            poster: item.poster_path,
            copertina: item.covers,
            //tipo
            media_type: item.media_type,
            gioco: item.platforms,
            //piattaforma/genere
            platform: item.platforms,
            genere_media: item.genre_ids,
            genere_gioco: item.genres,
            //date
            data_film: item.release_date,
            data_tv: item.first_air_date,
            data_gioco: item.releaseDate,

        }));
        const response = await axios.post(
            "https://gamediahub.it:5000/lists/save",
            {
                name: username,
                listname: listname,
                item: itemsToSave
            },
            { headers }
        );
        const data = response.data;
        if (data.msg === "List saved successfully") {
            console.log("Lista salvata con successo");
        } else {
            console.log("Errore durante il salvataggio della lista");
        }
        setIsListOpen(false);
        setCreatedList([]);
        setListname("")
        setIsListSaved(true)
        setIsAlertOpen(true)
        setTimeout(() => {
            setIsAlertOpen(false)
            setIsListSaved(false)
        }, 3500);
    };

    // API FILM, API SERIE TV, API GENERE, FORMATTAZIONE DATA

    const handleSubmit = async (event) => {
        event.preventDefault();
        setLoading(true);
      
        try {
          const [movies, games] = await Promise.all([
            fetch(`https://api.themoviedb.org/3/search/multi?api_key=ebdbb3f2b0075bfa9b6efb62a48a3aa3&language=it&query=${query}&page=1&include_adult=false`),
            fetch(`https://gamediahub.it:5000/igdb/games/?search=${query}`),
          ]);
        
          const [moviesData, gamesData] = await Promise.all([movies.json(), games.json()]);
          setTimeout(() => {
            setResults([...moviesData.results, ...gamesData.games]);
            setLoading(false);
            console.log(results)
            console.log("log")
          }, 1000);

          
        } catch (error) {
          console.log("Error while fetching data:", error);
          setLoading(false);
        }
      };

    const fetchAllGenres = async () => {
        const response = await fetch(`https://api.themoviedb.org/3/genre/movie/list?api_key=ebdbb3f2b0075bfa9b6efb62a48a3aa3&language=it`);
        const data = await response.json();
        const genresDict = {}
        data.genres.forEach(g => genresDict[g.id] = g.name);
        setAllGenres(genresDict);
    }
    useEffect(() => {
        fetchAllGenres()
    }, [])

    const formatItalianDate = (date) => {
        const italianDate = new Date(date).toLocaleDateString("it-IT");
        return italianDate;
    }

    // ORDINA RISULTATI E LISTE PER...

    const sortResults = () => {
        if (!sortType) {
            setSortType('title');
        }

        if (sortType === 'film') {
            setSortedResults(
                results.filter(result => result.media_type === "movie").sort((a, b) => {
                    const titleA = typeof a.title === 'string' ? a.title : '';
                    const titleB = typeof b.title === 'string' ? b.title : '';
                    return titleA.localeCompare(titleB);
                })
            );
        } else if (sortType === 'tv') {
            setSortedResults(
                results.filter(result => result.media_type === "tv").sort((a, b) => {
                    const titleA = typeof a.name === 'string' ? a.name : '';
                    const titleB = typeof b.name === 'string' ? b.name : '';
                    return titleA.localeCompare(titleB);
                })
            );
        } else if (sortType === 'game') {
            setSortedResults(
                results.filter(result => result.platforms ? result.platforms : null).sort((a, b) => {
                    const titleA = typeof a.title_game === 'string' ? a.title_game : '';
                    const titleB = typeof b.title_game === 'string' ? b.title_game : '';
                    return titleA.localeCompare(titleB);
                })
            );
            console.log(sortedResults)
        } else if (sortType === 'release_date') {
            setSortedResults(results.sort((a, b) => {
                return new Date(a.release_date || a.first_air_date || a.releaseDate) - new Date(b.release_date || b.first_air_date || b.releaseDate);
            }));
            console.log(sortedResults)
        } else if (sortType === 'title') {
            setSortedResults(
                results
                    .sort((a, b) => {
                        const titleA = a.media_type === "movie" ? (a.title || '') : (a.name || '');
                        const titleB = b.media_type === "movie" ? (b.title || '') : (b.name || '');
                        return titleA.localeCompare(titleB);
                    })
            );
        } else if (sortType === 'voto') {
            const sortedFilmsAndSeries = results.filter(result => result.media_type === "movie" || result.media_type === "tv")
              .sort((a, b) => b.vote_average - a.vote_average);
            const sortedGames = results.filter(result => result.slug)
              .sort((a, b) => b.metacritic - a.metacritic);
            setSortedResults([...sortedFilmsAndSeries, ...sortedGames]);
          } else {
            setSortedResults(results);
        }
    };

    const sortLists = () => {
        if (!sortType) {
            setSortType('title');
        }

        if (sortType === 'title') {
            setSortedResults(
                lists
                    .sort((a, b) => {
                        const titleA = typeof a.listname === 'string' ? a.listname : '';
                        const titleB = typeof b.listname === 'string' ? b.listname : '';
                        return titleA.localeCompare(titleB);
                    })
            );
            console.log(sortedResults)
        } else if (sortType === 'updatedAt') {
            setSortedResults(lists.sort((a, b) => {
                return new Date(a.updatedAt) - new Date(b.updatedAt);
            }));
            console.log(sortedResults)
        } else if (sortType === 'length') {
            setSortedResults(
                lists.sort((a, b) => b.item - a.item)
            );
            console.log(sortedResults)
        } else {
            return lists;
        }
    };

    return {
        isAlertOpen,
        isListOpen,
        isModalOpen,
        loading,
        createdList,
        listname,
        state,
        results,
        selectedResult,
        query,
        allGenres,
        sortedResults,
        sortType,
        name,
        lists,
        isDeleted,
        isEdited,
        isAuthenticated,
        isLoaded,
        isListSaved,
        isSelected,
        isBtnActive,
        theme,
        videos,
        setSortType,
        handleOpenModal,
        handleCloseModal,
        handleCreateList,
        handleRemoveResult,
        handleListNameChange,
        handleSaveList,
        handleSubmit,
        fetchAllGenres,
        formatItalianDate,
        setQuery,
        setListname,
        setIsModalOpen,
        setLoading,
        setSortedResults,
        sortResults,
        handleGetLists,
        handleEditListName,
        handleDeleteList,
        handleDeleteItem,
        setName,
        setLists,
        setIsDeleted,
        setIsEdited,
        setIsLoaded,
        sortLists,
        setIsListSaved,
        setIsAlertOpen,
        handleOpenList,
        setCreatedList,

    }
}

export default useLists