import axios from 'axios';
import { useState } from 'react';
import { useNavigate } from 'react-router-dom';



const ResendVerification = () => {
    const [email, setEmail] = useState('');
    const [message, setMessage] = useState('');
    const navigate = useNavigate()

    const requestNewVerificationLink = async (email) => {
        try {
            const response = await axios.post('https://gamediahub.it:5000/resendverification/request', { email });
            const data = response.data;
            setMessage(data.msg);
            return data
        } catch (error) {
            if(error){
                setMessage(error.response.data.msg);
                return error;
            }
            
        }
    };

    const handleClick = async () => {
        const response = await requestNewVerificationLink(email);
        setTimeout(() => {
            navigate("/")
        }, 3000)
    };

    return (
        <div className='form'>
            <div className='title'>INSERISCI EMAIL</div>
            <div className='formRow'>
                
                <div className='formInput'>
                    <label>
                        <input type="email" value={email} onChange={e => setEmail(e.target.value)} placeholder="Email" />
                    </label>
                </div>

                <button className='LoginButton' onClick={handleClick}>
                    Richiedi Link
                </button>
                {message && <p>{message}</p>}
            </div>
        </div>
    );
}

export default ResendVerification