import { useEffect } from "react"
import { useNavigate } from "react-router-dom"
import Typed from "react-typed"

const Registered = () => {
    const navigate = useNavigate()
    useEffect(() => {
        setTimeout(() => {
            navigate("/")
        }, 11000)
    })
    
    return (
        <div className="LandingContainer">
            <div className="verifyAccount">
                <Typed
                    typeSpeed={50}
                    fadeOut={false}
                    showCursor={true}
                    backSpeed={20}
                    cursorChar="_"
                    strings={[
                        `ACCOUNT REGISTRATO, CONTROLLA E VERIFICA L'EMAIL...`,
                        `VERRAI REINDIRIZZATO ALLA HOME TRA 2 SECONDI...`
                    ]}
                />
            </div>
        </div>
    )

}

export default Registered