import React, { useEffect, useRef, useState } from 'react';
import { OverlayScrollbarsComponent } from "overlayscrollbars-react";
import Modal from 'react-modal';
import 'overlayscrollbars/overlayscrollbars.css';
import { useDispatch, useSelector } from 'react-redux';
import jwt_decode from "jwt-decode";
import { useNavigate } from 'react-router-dom';
import useLists from '../hook/useLists';

function Search() {
  const isAuthenticated = useSelector(state => state.isAuthenticated);
  const dispatch = useDispatch();
  const {
    isAlertOpen, isListOpen, isModalOpen, loading, createdList, listname,
    state, results, selectedResult, query, allGenres, sortedResults,
    sortType, isListSaved, isSelected, isBtnActive, theme, videos,
    setSortType, sortResults, setSortedResults, handleOpenModal,
    handleCloseModal, handleCreateList, handleRemoveResult,
    handleListNameChange, handleSaveList, handleSubmit, fetchAllGenres,
    formatItalianDate, setQuery, setListname, setLoading, handleOpenList, setCreatedList } = useLists();


  const navigate = useNavigate();

  const bg = {
    overlay: {
      background: "transparent"
    }
  };

  const handleKeyDown = (event) => {
    if (event.key === 'KeyUp' || event.key === 'KeyDown') {
      event.preventDefault();
      handleSubmit()
    }
  }

  const handleBlur = () => {
    handleSubmit()
  }


  useEffect(() => {
    if (results.length) {
      sortResults();

    }
    setTimeout(() => {


      setLoading(false);
    }, 1500);
  }, [sortType, results]);

  useEffect(() => {
    const token = localStorage.getItem("jwtToken");
    if (token) {
      const decoded = jwt_decode(token);
      if (decoded.exp * 1000 < Date.now()) {
        console.log("logout")
        dispatch({ type: "LOGOUT" });
        navigate("/");
      } else {
        console.log("login")
        dispatch({ type: "LOGIN", payload: decoded });
      }
    }
  }, [dispatch, navigate]);

  useEffect(() => {
    fetchAllGenres()
  }, [])


  return (
    <>
      {isAuthenticated &&
        <div className="searchContainer">

          {/* SEARCHBAR */}
          <div className='searchForm'>

            <div className='labelSearch'>
              <label style={{ textShadow: "2px 2px 2px black", }}>CERCA FILM, SERIE TV O VIDEOGIOCHI</label>
            </div>

            <form style={{
              display: "flex",
              "flexDirection": "row",
              alignItems: "center",
              gap: "10px",
            }} onSubmit={handleSubmit}>
              <div className="searchButton" onClick={handleSubmit}>
                <div className='searchText'>
                  CERCA
                </div>
                <div>
                  <i class="fa-brands fa-searchengin"></i>
                </div>
              </div>
              <div className='searchBox'>
                <input className="searchInput" type="text" value={query} onChange={event => setQuery(event.target.value)}
                  placeholder="Cerca Film"
                  onKeyDown={handleKeyDown}
                  onBlur={handleBlur}
                  onKeyUp={handleKeyDown} />
                <div className="buttonSearch" type="submit"><i class="fas fa-search"></i></div>

              </div>

              {/* FILTRA PER */}
              <div className="FilterContainer">
                <select className="FilterBy Src" onChange={(event) => setSortType(event.target.value)}>
                  <option selected value="" >Filtra per...</option>
                  <option value="voto">Tutto</option>
                  <option value="voto">Voto/Metacritic</option>
                  <option value="title">Titolo</option>
                  <option value="release_date">Data</option>
                  <option value="film">Film</option>
                  <option value="tv">Serie TV</option>
                  <option value="game">Videogiochi</option>
                </select>
              </div>

            </form>
            {createdList.length > 0 &&
              <div className='BtnContainer'>
                <div className='ButtonLists OpenList'>
                  <div className='LoadBtn EffectBtn' onClick={handleOpenList}>
                    <svg>
                      <rect x="0" y="0" fill="none" width="100%" height="100%" />
                    </svg>
                    <div>
                      APRI LISTA
                    </div>
                  </div>
                </div>
              </div>}
          </div>

          {/* RISULTATI E SCROLLBAR */}
          <OverlayScrollbarsComponent className='scrollbar'
            element="span"
            options={{ scrollbars: { autoHide: 'scroll', theme: "os-theme-light" } }}

            defer>
            {/* CONTENITORE RISULTATI E LOADING */}
            <div className='resultsContainer'>

              {/* LOADING */}
              {loading ?
                <div className='loading'>
                  <div class="circle loader"></div>
                  <p className='textLoading'>CARICAMENTO...</p>
                </div> :
                /* MAPPA RISULTATI */

                sortedResults ? sortedResults.filter(result => result.release_date || result.releaseDate || result.first_air_date).map(result => (
                  result.title || result.name || result.title_game ? (
                    <div className="resultsList"
                      key={result.id}
                      style={{
                        backgroundRepeat: "repeat",
                        backgroundImage: `url(${result.media_type === "movie" || result.media_type === "tv" ?
                          `https://image.tmdb.org/t/p/w500${result.backdrop_path || result.poster_path || ""}`
                          : result.covers ? `${result.covers}` : ""})`,
                        backgroundSize: "cover", backgroundPosition: "50% 35%", imageRendering: "auto",
                      }}>

                      {/* CARD RISULTATO */}
                      <div className='infoContainer'>
                        <div className='movieTitle'>
                          {result.title && result.original_title ? (<span className='middle'>{result.title} - ({result.original_title})</span>)
                            : result.name ? (<span className='last'>{result.name}</span>)
                              : (<span className='last'>{result.title_game}</span>)}

                        </div>
                        <div className='middleContainer'>
                          <div className='year'>
                            <p>
                              <span>Data Release:</span>{
                                result.media_type == "movie"
                                  ? formatItalianDate(result.release_date)
                                  : result.media_type == "tv"
                                    ? formatItalianDate(result.first_air_date)
                                    : formatItalianDate(result.releaseDate)
                              }
                            </p>
                          </div>
                          <div className='type'>
                            <p>
                              <span>Tipo: </span>
                              {result.media_type == "movie" && "Film"}
                              {result.media_type == "tv" && "Serie TV"}
                              {result.platforms && "Videogioco"}
                            </p>
                          </div>
                          <div className='genre'>
                            {result.genre_ids ? (
                              <>
                                <div className='GenLabel'>Genere: </div>
                                <div className='GenContent'>{result.genre_ids.map(genre => allGenres[genre]).join(', ')}</div>

                              </>
                            ) : result.genres ? (
                              <>
                                <div className='GenLabel'>Genere: </div>
                                <div className='GenContent'>{result.genres}</div>

                              </>
                            ) : (
                              <>
                                <div className='GenLabel'>Genere: </div>
                                <div className='GenContent'>N/D</div>
                              </>
                            )}
                          </div>
                          <div className='tooltip'>
                            {result.genre_ids ? (
                              <>
                                <div className='GenLabel'>Genere: </div>
                                <div className='GenContent'>{result.genre_ids.map(genre => allGenres[genre]).join(', ')}</div>

                              </>
                            ) : result.genres ? (
                              <>
                                <div className='GenLabel'>Genere: </div>
                                <div className='GenContent'>{result.genres}</div>

                              </>
                            ) : (
                              <>
                                <div className='GenLabel'>Genere: </div>
                                <div className='GenContent'>N/D</div>
                              </>
                            )}
                          </div>
                          <div className='plat'>
                            {result.platforms ? (
                              <>
                                <div className='GenLabel'>Piattaforma: </div>
                                <div className='PlatContent'>
                                  {result.platforms.split(', ').map(platform => {
                                    switch (platform) {
                                      case 'PlayStation 3':
                                        return 'PS3';
                                      case 'PlayStation 4':
                                        return 'PS4';
                                      case 'PlayStation 5':
                                        return 'PS5';
                                      case 'Xbox 360':
                                        return 'x360';
                                      case 'Xbox One':
                                        return 'XOne';
                                      case 'PC (Microsoft Windows)':
                                        return "PC"
                                      default:
                                        return platform;
                                    }
                                  }).join(', ')}
                                </div>
                              </>
                            ) : null}
                          </div>
                          <div className='tooltip'>
                            {result.platforms ? (
                              <>
                                <div className='GenLabel'>Piattaforma: </div>
                                <div className='PlatContent'>
                                  {result.platforms.split(', ').map(platform => {
                                    switch (platform) {
                                      case 'PlayStation 3':
                                        return 'PS3';
                                      case 'PlayStation 4':
                                        return 'PS4';
                                      case 'PlayStation 5':
                                        return 'PS5';
                                      case 'Xbox 360':
                                        return 'x360';
                                      case 'Xbox One':
                                        return 'XOne';
                                      case 'PC (Microsoft Windows)':
                                        return "PC"
                                      // Aggiungi altri casi a seconda delle piattaforme che ti servono
                                      default:
                                        return platform;
                                    }
                                  }).join(', ')}
                                </div>
                              </>
                            ) : null}
                          </div>
                        </div>
                        <div className='overview' style={{ zIndex: "9999 !important" }} >
                          {result.overview ? (
                            <div className='overviewClick' onClick={() => handleOpenModal(result)}>
                              <span style={{ color: "white", textDecorationLine: "underline" }}>TRAMA</span>
                            </div>
                          ) : result.summary ? (
                            <div className='overviewClick' onClick={() => handleOpenModal(result)}>
                              <span style={{ color: "white", textDecorationLine: "underline" }}>TRAMA</span>
                            </div>
                          ) : (
                            <span style={{ color: "red" }}>TRAMA: N/D</span>
                          )}
                          {/* POPUP TRAMA */}
                          <Modal
                            closeTimeoutMS={500}
                            style={bg}
                            className="PopUpContainer"
                            isOpen={isModalOpen}
                            onRequestClose={handleCloseModal}
                            contentLabel="Example Modal"
                          >
                            <div className="PopUpOv ">
                              {selectedResult.media_type ? (<p>{selectedResult.overview}</p>)
                                : selectedResult.summary ? (
                                  `${selectedResult.summary}`
                                ) : ""
                              }
                              <button className={`PopUpCloser`} onClick={handleCloseModal}>X</button>
                            </div>
                          </Modal>
                          {/* POPUP CREAZIONE LISTA */}
                          <Modal
                            closeTimeoutMS={500}
                            style={bg}
                            className="PopUpContainer"
                            isOpen={isListOpen} onRequestClose={handleCloseModal}
                            contentLabel="Example Modal">
                            {isListOpen &&

                              <div className='PopUpOv '>
                                <form onSubmit={handleSaveList}>

                                  {/* INPUT NOME LISTA */}
                                  <label>
                                    <input required type="text" placeholder="Inserisci il nome della lista" value={listname} onChange={e => setListname(e.target.value.toUpperCase())} />
                                  </label>
                                  {/* LISTA */}
                                  <h2>Risultati selezionati</h2>
                                  <div className='list'>
                                    {results.length && createdList.map(result => (
                                      <div className="itemList" key={result.id}>
                                        <div className='title'>
                                          {result.title && <span>{result.title}</span>}
                                          {result.name && <span>{result.name}</span>}
                                          {result.title_game && <span>{result.title_game}</span>}</div>
                                        <div className='buttonListRemove'>
                                          <div className='RemoveBtn'>
                                            <div onClick={() => handleRemoveResult(result)}><i class="fa-solid fa-xmark"></i></div>
                                          </div>
                                        </div>
                                      </div>
                                    ))}
                                  </div>
                                  {/* PULSANTE SALVATAGGIO */}
                                  <div className="containerListSave">
                                    <div className='buttonListSave' >

                                      <button className="SaveBtn" type='submit'>
                                        <svg>
                                          <rect x="0" y="0" fill="none" width="100%" height="100%" />
                                        </svg>
                                        <div>
                                          Salva
                                        </div>
                                      </button>

                                    </div>

                                  </div>
                                </form>
                                <button className='PopUpCloser' onClick={handleCloseModal}>X</button>

                              </div>
                            }</Modal>
                          {/* NOTIFICA RISULTATO GIA' IN LISTA */}
                          <Modal
                            style={{
                              zIndex: "9999 !important", position: "fixed", overlay: {
                                backgroundColor: 'transparent'
                              }
                            }}
                            className="PopUpContainer Alert"
                            isOpen={isAlertOpen} onRequestClose={handleCloseModal}
                            contentLabel="Example Modal">
                            <div className='PopUpOv'>
                              {isListSaved && <h3>Lista salvata con successo</h3>}
                              {isSelected && <h3>Risultato già presente nella lista</h3>}
                              <button className='PopUpCloser' onClick={handleCloseModal}>X</button>
                            </div>
                          </Modal>
                        </div>
                        {/* CONTENITORE VOTO E PULSANTE SELEZIONA */}
                        <div className="listAndVoteBigContainer">

                          <div className='buttonListAndVote'>

                            <div className="SaveBtn" onClick={() => handleCreateList(result)}>
                              <svg>
                                <rect x="0" y="0" fill="none" width="100%" height="100%" />
                              </svg>
                              <div>
                                {createdList.includes(result) ? "Selezionato" : "Seleziona"}
                              </div>
                            </div>
                            <div> {result.vote_average ? (
                              <p style={{ color: "#EB9532" }}>VOTO: <span>{Math.floor(result.vote_average * 100) / 100}</span></p>) :
                              result.vote_average === 0 ? (
                                <p style={{ color: "#EB9532" }}>VOTO: <span style={{ color: "red", fontWeight: "900" }}>N/D</span></p>) :
                                result.vote ? (<p style={{ color: "#EB9532" }}>METACRITIC: <span>{Math.floor(result.vote * 1) / 1}</span></p>) :
                                  result.video ? (<a href={`https://www.youtube.com/watch?v=${result.video}`} style={{ color: "#EB9532", fontWeight: "900" }}>GAMEPLAY</a>) :
                                    (<p style={{ color: "red", textDecorationLine: "line-through", fontWeight: "900" }}>GAMEPLAY</p>)}
                            </div>
                          </div>
                        </div>
                      </div>

                    </div>



                  ) : <div style={{ display: "flex", height: "300px", flexDirection: "column", justifyContent: "space-evenly", alignItems: "center", alignContent: "center", textAlign: "center" }}><h1>CERCA UN FILM O UNA SERIE TV</h1><p>Nessun risultato</p></div>
                )) : <div style={{ display: "flex", height: "300px", flexDirection: "column", justifyContent: "space-evenly", alignItems: "center", alignContent: "center", textAlign: "center" }}><h1>CERCA UN FILM O UNA SERIE TV</h1><p>Nessun risultato</p></div>
              }


            </div>
          </OverlayScrollbarsComponent >

        </div >}
    </>
  );
}

export default Search;