import React, { useEffect, useState } from 'react'
import axios from 'axios';
import { Link, useNavigate } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import Modal from 'react-modal';
import ForgotPassword from './ForgotPassword';

const Login = ({ onClick }) => {
    const dispatch = useDispatch();
    const isAuthenticated = useSelector(state => state.isAuthenticated);
    const [name, setName] = useState('')
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const [msg, setMsg] = useState('');
    const navigate = useNavigate();
    const [showModalReset, setShowModalReset] = useState(false);
    const closeModalResetDelay = () => {
        setTimeout(() => {
            setShowModalReset(false)
        }, 3000)
    }

    const closeModalReset = () => {
        setShowModalReset(false)
    }



    const openModalReset = () => setShowModalReset(true);
    const bg = {
        overlay: {
            background: "transparent"
        }
    };

    const Auth = async (e) => {
        e.preventDefault();
        try {
            await axios.post('https://gamediahub.it:5000/login', {
                name: name,
                password: password,
            }, { withCredentials: true });  
            dispatch({ type: 'LOGIN' });
            console.log(isAuthenticated)
        } catch (error) {
            if (error.response) {
                setMsg(error.response.data.msg);
            }
        }
        
    }



    return (
        <>
            {!isAuthenticated &&
                <>
                    <form onSubmit={Auth}>
                        <div className="formRow">
                            <div className="formInput">
                                <label>
                                    <input required type="text" value={name} onChange={e => setName(e.target.value)} placeholder="Username" />
                                </label>
                            </div>
                        </div>
                        {/* <input type="text" value={username} onChange={e => setUsername(e.target.value)} placeholder="Username" /> */}
                        <div className="formRow">
                            <div className="formInput">
                                <label>

                                    <input required type="password" value={password} onChange={e => setPassword(e.target.value)} placeholder="Password" />
                                </label>
                            </div>

                        </div>



                        <div className='PopUpText' style={{ paddingBottom: "0" }}>Inserisci email e password</div>

                        <div className="Psw">
                            <div className="PswText">
                                <span onClick={openModalReset}>Recupera Password.</span>
                            </div>
                        </div>

                        <button className='LoginButton' type="submit" onClick={onClick}>Login</button>
                        {msg && <p>{msg}</p>}
                    </form>

                    <Modal className="PopUpContainer ModalLogin animated bounceIn " closeTimeoutMS={500}
                        style={bg} isOpen={showModalReset} onRequestClose={showModalReset}>
                        <div className='PopUp PswReset'>
                            <div className='PopUpTitle Reset' >RECUPERA PASSWORD</div>

                            <ForgotPassword closeModal={closeModalResetDelay} />

                            <div className='PopUpCloser' onClick={closeModalReset}>
                                X
                            </div>
                            <div className="RegisterText">Se non hai un account <span>REGISTRATI QUI</span>
                            </div>

                        </div>
                    </Modal>
                </>}

        </>
    )
}

export default Login