import React, { useEffect, useState } from "react";
import axios from "axios";
import { useParams } from "react-router-dom";
import Modal from 'react-modal';

const Reset = ({ match }) => {
    const [password, setPassword] = useState("");
    const [msg, setMsg] = useState('');
    const { token } = useParams()
    const bg = {
        overlay: {
            background: "transparent"
        }
    };

    const [showModalReset, setShowModalReset] = useState(false);
    const closeModalReset = () => setShowModalReset(false);
    const openModalReset = () => setShowModalReset(true);

    useEffect(() => {
        openModalReset()
    }, [])

    const handleSubmit = async (e) => {
        e.preventDefault();
        try {
            const { data } = await axios.post(`https://gamediahub.it:5000/resetPassword/${token}`, {
                password
            });
            setMsg(data.msg);
        } catch (error) {
            console.log(error)
        }
    };

    return (
        <Modal className="PopUpContainer ModalLogin animated bounceIn" closeTimeoutMS={500}
            style={bg} isOpen={showModalReset} onRequestClose={showModalReset}>
            <div className='PopUp'>
                <div className='PopUpTitle'>RECUPERA PASSWORD</div>
                <form onSubmit={handleSubmit}>
                    <div className="formRow">
                        <div className="formInput">
                            <label>
                                <input required type="password" value={password} onChange={e => setPassword(e.target.value)} placeholder="New Password" />
                            </label>
                        </div>
                    </div>
                    <button type="submit">Reset Password</button>
                    {msg && <p>{msg}</p>}
                </form>
                <div className='PopUpCloser' onClick={closeModalReset}>
                    X
                </div>

            </div>
        </Modal>
    );
};

export default Reset