import axios from "axios";
import { useEffect, useState } from "react";
import { Link, useNavigate } from "react-router-dom";

function Registration({ closeModal }) {
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [confPassword, setConfPassword] = useState('');
  const [email, setEmail] = useState('');
  const [msg, setMsg] = useState('')
  const [isOpen, setIsOpen] = useState(false)
  const [formSubmitted, setFormSubmitted] = useState(false)
  const navigate = useNavigate();

  async function handleSubmit(event) {
    event.preventDefault();
    try {
      const { data } = await axios.post("https://gamediahub.it:5000/users", {
        name: username,
        password: password,
        email: email,
        confPassword: confPassword
      });
      setFormSubmitted(true);
      console.log(formSubmitted)
      closeModal();
    } catch (error) {
      if (error.response) {
        setMsg(error.response.data.msg);
      }
    }
  }

  useEffect(() => {
    async function navigateToRegistered() {
        if (formSubmitted) {
            setTimeout(() => {
            navigate("/registered");
            }, 2000);
        } 
    }
    navigateToRegistered();
}, [formSubmitted]);


  return (
    <form onSubmit={handleSubmit}>
      <div className="formRow">
        <div className="formInput">
          <label>
            Username
            <input type="text" value={username} onChange={e => setUsername(e.target.value)} placeholder="Username" />
          </label>
        </div>
      </div>
      <div className="formRow">
        <div className="formInput">
          <label>
            Password
            <input type="password" value={password} onChange={e => setPassword(e.target.value)} placeholder="******" />
          </label>
        </div>
      </div>
      <div className="formRow">
        <div className="formInput">
          <label>
            Conferma Password
            <input className="inputFilm" type="password" value={confPassword} onChange={e => setConfPassword(e.target.value)} placeholder="******" />
          </label>
        </div>
      </div>
      <div className="formRow">
        <div className="formInput">
          <label>
            Indirizzo Email
            <input type="email" value={email} onChange={e => setEmail(e.target.value)} placeholder="example@example.com" />
          </label>
        </div>
      </div>
    {msg && msg}
      <button className="LoginButton" type="submit">REGISTRATI</button>

    </form>
  )
}

export default Registration;