import { useEffect, useState } from "react";
import Typed from "react-typed"


const AboutMe = () => {

    const [showDiv, setShowDiv] = useState(true);
    const [showBeDiv, setShowBeDiv] = useState(false)
    const [showButton, setShowButton] = useState(false)

    useEffect(() => {
        setTimeout(() => {
            setShowButton(true);
        }, 5000);
    }, []);


    const handleContinue = () => {
        if (showDiv) {
            setShowDiv(false);
            setShowBeDiv(true);
        } else {
            setShowDiv(true);
            setShowBeDiv(false);
        }

    };

    return (
        <div className="AboutMeContainer">
            {showDiv && <div className="Content">
                <div className="Text">
                    <Typed
                        startDelay={2000}
                        typeSpeed={20}
                        fadeOut={true}
                        backSpeed={20}
                        smartBackspace={true}
                        className="FirstP"
                        showCursor={false}
                        cursorChar="|"
                        strings={[
                            'Ciao! Mi chiamo Andrea e questo è un mio progetto.',
                        ]}
                    />
                </div>
                <div className="Text">
                    <Typed
                        className="SecondP"
                        strings={[
                            `É utile per cercare Film`, `É utile per cercare Serie Tv`, `É utile per cercare Film e Serie TV    `, `É utile per cercare Film e Serie TV 😊`
                        ]}
                        startDelay={5000}
                        backSpeed={80}
                        smartBackspace={true}
                        typeSpeed={20}
                        backDelay={100}
                        showCursor={false} />
                </div>
                <div className="Text">
                    <Typed
                        className="SecondP"
                        strings={[
                            `Trovi anche le relative informazioni su trama, data di uscita, genere, ecc...`
                        ]}
                        startDelay={10000}
                        typeSpeed={20}
                        showCursor={false} />
                </div>
                <div className="Text">
                    <Typed
                        className="SecondP"
                        strings={[
                            'Pensa che puoi anche creare la tue liste di film e serie tv che preferisci',
                            'Pensa che puoi anche creare la tue liste di film e serie tv che detesti',
                            'Pensa che puoi anche creare la tue liste di film e serie tv che preferisci o detesti!          ',
                            'Pensa che puoi anche creare la tue liste di film e serie tv che preferisci o detesti! Entrambe le cose!         Puoi anche modificare il titolo della lista e cancellare la serie o il film da quella lista!',
                        ]}
                        startDelay={17000}
                        backSpeed={80}
                        showCursor={false}
                        smartBackspace={true}
                        typeSpeed={20} />
                </div>
                <div className="Text">
                    <Typed
                        className="SecondP"
                        strings={[`Per realizzare questo progetto ho usato l'API di TMDb.org. </br> 
                        Per lo stile ho utilizzato SCSS.</br>
                        Come librerie Frontend ho utilizzato:<ul><li>ReactJS,</li><li>React Redux per lo gestire lo stato di autenticazione nei vari componenti,</li><li>
                        Overlayscrollbars-react per personalizzare la scrollbar,</li><li>React-modal per creare i Popup,</li><li>
                        React Router Dom per creare le Route</li><li>React-Type per creare questa pagina</li></ul>`
                        ]}
                        startDelay={31000}
                        showCursor={false}
                        typeSpeed={20} />
                </div>

                {showButton && <div className='BtnContainer'>
                    <div className='ButtonLists'>
                        <div className='LoadBtn EffectBtn' onClick={handleContinue}>
                            <svg>
                                <rect x="0" y="0" fill="none" width="100%" height="100%" />
                            </svg>
                            <div>
                                AVANTI
                            </div>
                        </div>
                    </div>
                </div>}
            </div>}

            {showBeDiv &&
                <div className="Content">
                    <div className="Text">
                        <Typed
                            startDelay={2000}
                            typeSpeed={20}
                            fadeOut={true}
                            backSpeed={20}
                            smartBackspace={true}
                            className="FirstP"
                            showCursor={false}
                            cursorChar="|"
                            strings={[
                                `Per il backend ho utilizzato:`,
                            ]}
                        />
                    </div>
                    <div className="Text">
                        <Typed
                            className="SecondP"
                            strings={[
                                `<ul><li> NodeJS come ambiente di lavoro</li><li> Express come framework</li>
                                <li>Sendgrid per il servizio email per la registrazione dell'utente</li>
                                <li>JS-Cookie per gestire i cookies</li><li>Jsonwebtoken come token di accesso e di verifica
                                </li><li> Mysql2 come gestione del DB</li><li> Sequelize come Nodo del DB</li><li> Bcrypt per criptare le password
                                </li>
                                </ul>`,
                            ]}
                            startDelay={5000}
                            backSpeed={80}
                            smartBackspace={true}
                            typeSpeed={20}
                            backDelay={800}
                            showCursor={false} />
                    </div>
                    <div className="Text">
                        <Typed
                            className="SecondP"
                            strings={[`Questo progetto mi ha insegnato molto.</br>
                            É stata la prima volta che ho lavorato sia Frontend che Backend.</br>
                            Sicuramente non è un sito privo di difetti o bug, ma spero che possa aiutarmi a trovare un lavoro e spero anche che vi piaccia!`
                            ]}
                            onStringTyped={() => setShowButton(true)}
                            startDelay={20000}
                            showCursor={false}
                            typeSpeed={20} />
                    </div>
                    <div className='BtnContainer'>
                        <div className='ButtonLists'>
                            <div className='LoadBtn EffectBtn' onClick={handleContinue}>
                                <svg>
                                    <rect x="0" y="0" fill="none" width="100%" height="100%" />
                                </svg>
                                <div>
                                    INDIETRO
                                </div>
                            </div>
                        </div>
                    </div>
                </div>}
        </div>
    )
}

export default AboutMe